import NavDom from '@/self-components/nav'
import Logo from '@/self-components/logo'
import * as echarts from 'echarts'
import FooterDom from '@/self-components/footer'

export default {
    name: 'PAGE_PARTNER',
    components: {
		NavDom,
        Logo,
        FooterDom
    },
    data() {
        return {
            width: 0,
            height: 0,
            inputGroups: [
                {
                    title: '场地费用(月租)',
                    value: '',
                    unit: '元'
                },
                {
                    title: '人工费用(月支出)',
                    value: '',
                    unit: '元'
                },
                {
                    title: '运营时长',
                    value: '',
                    unit: '小时/天'
                },
                {
                    title: '充电桩数量(直流)',
                    value: '',
                    unit: '个'
                },
                {
                    title: '充电桩数量(交流)',
                    value: '',
                    unit: '个'
                }
            ]
        }
    },
    mounted() {
		this.width = document.documentElement.clientWidth
		this.height = document.documentElement.clientHeight
        this.initCharts()
    },
    head () {
        return {
            title: '举视合伙人',
            meta: [
                    { hid: 'description', name: 'description', content: '充电站城市合伙人' },
                    { hid: 'keywords', name: 'keywords', content: '举视合伙人' },  
                ]
        }
    },
    async asyncData(app) {
        let result = await app.$axios.get('/init')
        let menu = await app.$axios.get('/menus')
		menu = menu.data.data
		result = result.data.data
		return { result, menu }
	},
    methods: {
        handleClick() {
            let result = false
            this.inputGroups.forEach(item => {
                if (item.value == '') result = true
            })
            if (result) return alert('请填写完整后查看结果')
        },
        /**
         * 点击方块获取输入框光标
         * @param { number } index 当前点击的下角标
         */
        handleSelectInput(index) {
            this.$refs[`input${ index }`][0].focus()
        },
        /**
         * 初始化图标       
         */
        initCharts() {
            const element = this.$refs.charts
            const charts = echarts.init(element)
            const option = {
                title: {
                    text: '模拟收益'
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: { 
                        type: 'shadow'
                    }
                },
                legend: {
                    data: ['成本费用', '收入'],
                    right: 20
                },
                grid: {
                    bottom: 10,
                    left: 40,
                    right: 40,
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    // boundaryGap: false,
                    data: ['第一个月','第二个月','第三个月','第四个月','第五个月','第六个月','第七个月','第八个月','第九个月','第十个月','第十一个月','第十二个月']
                },
                yAxis: [
                    {
                        type: 'value',
                        name: '金额（元）',
                        max: 10000,
                        min: 0,
                    },
                    {
                        type: 'value',
                        name: '收益率（%）',
                        min: 0,
                        max: 100
                    }
                ],
                series: [
                    {
                        name: '收益率（%）',
                        data: [32, 40, 43, 46, 60, 68, 74, 73, 80, 85, 87, 90],
                        type: 'line',
                        smooth: true,
                        symbol: 'none',
                        yAxisIndex: 1,
                        areaStyle: {
                            color: new echarts.graphic.LinearGradient(0, 0 , 0, 1, [
                                {
                                    offset: 0,
                                    color: 'rgba(255,131,110, .02)'
                                },
                                {
                                    offset: 1,
                                    color: '#fff'
                                }
                            ])
                        },
                        itemStyle: {
                            normal: {
                                color: '#FF6F68',
                                lineStyle: {
                                    color: '#FF6F68'
                                }
                            }
                        }
                    },
                    {
                        name: '成本费用',
                        type: 'bar',
                        barWidth: '15%',
                        barGap: '20%',
                        yAxisIndex: 0,
                        data: [4000, 4000, 4000, 4000, 6200, 6200, 5000, 5000, 6200, 6000, 5800, 5600],
                        itemStyle: {
                            normal: {
                                barBorderRadius:[15, 15, 0, 0],
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    {
                                        offset: 0,
                                        color: '#4188F3'
                                    },
                                    {
                                        offset: 1,
                                        color: 'rgba(77,170,247, .2)'
                                    }
                                ])
                            }
                        }
                    },
                    {
                        name: '收入',
                        type: 'bar',
                        barWidth: '15%',
                        barGap: '20%',
                        yAxisIndex: 0,
                        data: [3600, 3600, 3600, 3600, 5200, 5200, 5000, 5000, 7200, 8000, 8000, 8400],
                        itemStyle: {
                            normal: {
                                barBorderRadius:[15, 15, 0, 0],
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    {
                                        offset: 0,
                                        color: '#FDB32A'
                                    },
                                    {
                                        offset: 1,
                                        color: 'rgba(255,218,106, .2)'
                                    }
                                ])
                            }
                        }
                    }
                ]
            }
            charts.setOption(option)
        }
    }
}
